import styled from 'styled-components';
import { useState, useEffect, useCallback } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';

// 
import numerologyIcon from '../assets/Logo/numerology.png';
import crystalBallIcon from '../assets/Logo/crystal-ball.png';
import solutionIcon from '../assets/Logo/solution (1).png';
import surpriseBoxIcon from '../assets/Logo/surprise-box.png';
import SurpriseGenerator from '../components/SurpriseGenerator';


// 
import NumerologyCalculator from '../components/NumerologyCalculator';

// 
import InspirationTemplates from '../components/InspirationTemplates';

const PageContainer = styled.div`
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const PageTitle = styled.h2`
  margin-bottom: 10px;
  color: #333;
  text-align: center;
`;

const PageSubtitle = styled.p`
  margin-bottom: 30px;
  color: #666;
  text-align: center;
  font-size: 14px;
`;

const OptionsGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  width: 100%;
  max-width: 500px;
`;

const OptionCard = styled.div`
  background: #fff;
  border-radius: 12px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  cursor: pointer;
  
  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
  }
`;

const IconContainer = styled.div`
  width: 60px;
  height: 60px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Icon = styled.img`
  width: 100%;
  height: 100%;
  object-fit: contain;
`;

const OptionTitle = styled.h3`
  font-size: 16px;
  color: #333;
  margin: 0;
  text-align: center;
`;

const BackButton = styled.button`
  padding: 8px 16px;
  background: #f0f0f0;
  border: none;
  border-radius: 4px;
  margin-bottom: 20px;
  cursor: pointer;
  font-size: 14px;
  
  &:hover {
    background: #e0e0e0;
  }
`;

const HelperPage = () => {
  const navigate = useNavigate();
  const location = useLocation();
  
  //  URL 
  const getPageFromURL = () => {
    //  URL 
    const currentURL = window.location.href;
    console.log("URL:", currentURL);
    
    const urlParams = new URLSearchParams(window.location.search);
    const page = urlParams.get('page');
    console.log("URL :", page);
    
    return page;
  };
  
  // : URL  > localStorage > null
  const [selectedOption, setSelectedOption] = useState(() => {
    const urlPage = getPageFromURL();
    if (urlPage) {
      console.log(" URL :", urlPage);
      return urlPage;
    }
    
    const savedPage = localStorage.getItem('helper_page');
    if (savedPage) {
      // 
      localStorage.removeItem('helper_page');
      console.log(" localStorage :", savedPage);
      return savedPage;
    }
    
    console.log(" null");
    return null;
  });
  
  //  URL 
  useEffect(() => {
    //  URL 
    const searchParams = new URLSearchParams(location.search);
    const hashParams = location.hash ? new URLSearchParams(location.hash.substring(1)) : null;
    
    // 
    let pageParam = searchParams.get('page');
    
    //  hash 
    if (!pageParam && hashParams) {
      pageParam = hashParams.get('page');
    }
    
    //  localStorage
    if (!pageParam) {
      pageParam = localStorage.getItem('helper_page');
    }
    
    if (pageParam) {
      setSelectedOption(pageParam);
      localStorage.removeItem('helper_page');
    }
  }, [location]);
  
  // 
  const resetSelectedOption = useCallback(() => {
    setSelectedOption(null);
    //  localStorage 
    localStorage.removeItem('crystal_color_filter');
    localStorage.removeItem('filter_timestamp');
    localStorage.removeItem('redirect_to_helper');
    localStorage.removeItem('helper_page');
  }, []);
  
  //  URL 
  useEffect(() => {
    const path = location.pathname;
    const search = location.search;
    
    //  URL  /helper 
    if (path === '/helper' && !search) {
      resetSelectedOption();
    }
  }, [location, resetSelectedOption]);
  
  const handleOptionClick = (option) => {
    setSelectedOption(option);
  };
  
  const renderContent = () => {
    console.log(" HelperPage :", selectedOption);
    
    switch (selectedOption) {
      case 'numerology':
        return <NumerologyCalculator />;
      case 'guidance':
        return <div></div>;
      case 'inspiration':
        return <InspirationTemplates />;
      case 'surprise':
        return <SurpriseGenerator />;
      case 'ai-advisor':
        return <div></div>;
      default:
        return null; // 
    }
  };
  
  const handleBackClick = () => {
    resetSelectedOption();
    navigate('/helper', { replace: true });
  };
  
  return (
    <PageContainer>
      <PageTitle></PageTitle>
      <PageSubtitle></PageSubtitle>
      
      {selectedOption ? (
        <>
          <BackButton onClick={handleBackClick}></BackButton>
          {renderContent()}
        </>
      ) : (
        <OptionsGrid>
          <OptionCard onClick={() => handleOptionClick('numerology')}>
            <IconContainer>
              <Icon src={numerologyIcon} alt="" />
            </IconContainer>
            <OptionTitle></OptionTitle>
          </OptionCard>
          
          <OptionCard onClick={() => handleOptionClick('guidance')}>
            <IconContainer>
              <Icon src={crystalBallIcon} alt="" />
            </IconContainer>
            <OptionTitle></OptionTitle>
          </OptionCard>
          
          <OptionCard onClick={() => handleOptionClick('inspiration')}>
            <IconContainer>
              <Icon src={solutionIcon} alt="" />
            </IconContainer>
            <OptionTitle></OptionTitle>
          </OptionCard>
          
          <OptionCard onClick={() => handleOptionClick('surprise')}>
            <IconContainer>
              <Icon src={surpriseBoxIcon} alt="" />
            </IconContainer>
            <OptionTitle></OptionTitle>
          </OptionCard>
        </OptionsGrid>
      )}
    </PageContainer>
  );
};

export default HelperPage; 