import React, { useState } from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import { useDesign } from '../contexts/DesignContext';

const PageTitle = styled.h2`
  margin-bottom: 30px;
  color: #333;
  text-align: center;
`;

const InputContainer = styled.div`
  background: #f5f5f5;
  border-radius: 20px;
  padding: 30px;
  width: 100%;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
`;

const InputRow = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 20px;
`;

const InputField = styled.input`
  padding: 12px 15px;
  border-radius: 30px;
  border: 1px solid #ddd;
  width: 100%;
  font-size: 16px;
  text-align: center;
  background: white;
  
  &:focus {
    outline: none;
    border-color: #4a90e2;
  }
`;

const CalculateButton = styled.button`
  padding: 12px 20px;
  background: #f0f0f0;
  color: #333;
  border: none;
  border-radius: 30px;
  font-size: 16px;
  cursor: pointer;
  width: 100%;
  max-width: 300px;
  transition: all 0.3s ease;
  
  &:hover {
    background: #e0e0e0;
  }
`;

const ResultContainer = styled.div`
  width: 100%;
  max-width: 500px;
  text-align: center;
`;

const ResultTitle = styled.h3`
  margin-bottom: 15px;
  color: #333;
`;

const ResultText = styled.p`
  margin-bottom: 15px;
  color: #666;
  line-height: 1.6;
`;

const NumerologyCalculator = () => {
  const navigate = useNavigate();
  const { setCurrentDesign } = useDesign();
  const [year, setYear] = useState('');
  const [month, setMonth] = useState('');
  const [day, setDay] = useState('');
  const [result, setResult] = useState(null);

  const calculateLifeNumber = () => {
    if (!year || !month || !day) {
      alert('');
      return;
    }

    // 
    const dateString = `${year}${month}${day}`;
    let sum = 0;
    
    for (let i = 0; i < dateString.length; i++) {
      sum += parseInt(dateString[i]);
    }
    
    // 
    while (sum > 9) {
      let tempSum = 0;
      sum.toString().split('').forEach(digit => {
        tempSum += parseInt(digit);
      });
      sum = tempSum;
    }
    
    setResult(sum);
  };

  const getLifeNumberMeaning = (number) => {
    const meanings = {
      1: {
        title: '',
        description: [
          '1',
          '',
          '1',
          ''
        ],
        role: '',
        thinking: '',
        colors: ['', '']
      },
      2: {
        title: '',
        description: [
          '2',
          '12',
          '2',
          '2'
        ],
        role: '',
        thinking: '',
        colors: ['']
      },
      3: {
        title: '',
        description: [
          '3',
          '312',
          '33',
          '3'
        ],
        role: '',
        thinking: '',
        colors: ['']
      },
      4: {
        title: '',
        description: [
          '4',
          '4',
          '4',
          '4'
        ],
        role: '',
        thinking: '',
        colors: ['']
      },
      5: {
        title: '',
        description: [
          '5',
          '5',
          '5'
        ],
        role: '',
        thinking: '',
        colors: ['']
      },
      6: {
        title: '',
        description: [
          '6',
          '63',
          '6',
          '6'
        ],
        role: '',
        thinking: '',
        colors: ['']
      },
      7: {
        title: '',
        description: [
          '77',
          '',
          '7',
          ''
        ],
        role: '',
        thinking: '',
        colors: ['']
      },
      8: {
        title: '',
        description: [
          '88',
          '8',
          ''
        ],
        role: '',
        thinking: '',
        colors: ['']
      },
      9: {
        title: '',
        description: [
          '9',
          '99',
          '',
          ''
        ],
        role: '',
        thinking: '',
        colors: ['']
      }
    };
    
    return meanings[number] || {
      title: '',
      description: [''],
      role: '',
      thinking: '',
      colors: []
    };
  };

  const handleColorCrystalClick = (color) => {
    // 
    localStorage.setItem('crystal_color_filter', color);
    localStorage.setItem('filter_timestamp', Date.now().toString());
    
    console.log(` ${color} ...`);
    
    try {
      //  HashRouter 
      localStorage.setItem('redirect_to_helper', 'true');
      localStorage.setItem('helper_page', 'inspiration');
      
      //  HashRouter #/helper
      window.location.href = `${window.location.origin}${process.env.PUBLIC_URL}/#/helper`;
    } catch (error) {
      console.error(":", error);
      navigate('/helper');
    }
  };

  return (
    <>
      <PageTitle></PageTitle>
      
      <InputContainer>
        <InputRow>
          <InputField 
            type="number" 
            placeholder="" 
            value={year}
            onChange={(e) => setYear(e.target.value)}
          />
        </InputRow>
        
        <InputRow>
          <InputField 
            type="number" 
            placeholder="" 
            min="1" 
            max="12"
            value={month}
            onChange={(e) => setMonth(e.target.value)}
          />
        </InputRow>
        
        <InputRow>
          <InputField 
            type="number" 
            placeholder="" 
            min="1" 
            max="31"
            value={day}
            onChange={(e) => setDay(e.target.value)}
          />
        </InputRow>
        
        <CalculateButton onClick={calculateLifeNumber}>
          
        </CalculateButton>
      </InputContainer>
      
      {result && (
        <ResultContainer>
          <ResultTitle></ResultTitle>
          <h1 style={{ fontSize: '60px', margin: '10px 0' }}>{result}</h1>
          <div style={{ margin: '20px 0', fontWeight: 'bold' }}>
            {getLifeNumberMeaning(result).title.split('')[1] || ''}
          </div>
          
          <ResultText>{getLifeNumberMeaning(result).title}</ResultText>
          
          {getLifeNumberMeaning(result).description.map((paragraph, index) => (
            <ResultText key={index}>{paragraph}</ResultText>
          ))}
          
          <div style={{ margin: '30px 0' }}>
            <div>{getLifeNumberMeaning(result).role}</div>
            <div>{getLifeNumberMeaning(result).thinking}</div>
            <div>{getLifeNumberMeaning(result).colors.join('')}</div>
          </div>
          
          {getLifeNumberMeaning(result).colors.map((color, index) => (
            <div key={index} style={{ margin: '10px 0' }}>
              <a 
                onClick={() => handleColorCrystalClick(color)}
                style={{ 
                  display: 'inline-block',
                  padding: '10px 20px',
                  border: '1px solid #ddd',
                  borderRadius: '30px',
                  textDecoration: 'none',
                  color: '#333',
                  cursor: 'pointer'
                }}
              >
                {color}
              </a>
            </div>
          ))}
        </ResultContainer>
      )}
    </>
  );
};

export default NumerologyCalculator; 