import styled from 'styled-components';
import { useDesign } from '../../contexts/DesignContext';

const InfoContainer = styled.div`
  padding: 20px;
`;

const Title = styled.h2`
  font-size: 24px;
  color: #333;
  margin-bottom: 16px;
`;

const Section = styled.div`
  margin-bottom: 24px;
`;

const SectionTitle = styled.h3`
  font-size: 16px;
  color: #666;
  margin-bottom: 8px;
`;

const PropertyList = styled.ul`
  list-style: none;
  padding: 0;
`;

const PropertyItem = styled.li`
  margin-bottom: 8px;
  color: #333;
`;

const ProductInfo = () => {
  const { selectedCrystal } = useDesign();

  if (!selectedCrystal) {
    return null;
  }

  //  description 
  const descriptions = selectedCrystal.description || [];

  return (
    <InfoContainer>
      <Title>{selectedCrystal.name}</Title>
      
      <Section>
        <SectionTitle></SectionTitle>
        <PropertyList>
          <PropertyItem>{selectedCrystal.color}</PropertyItem>
          <PropertyItem>{selectedCrystal.size} mm</PropertyItem>
          <PropertyItem>{selectedCrystal.energy}</PropertyItem>
          <PropertyItem>NT$ {selectedCrystal.price}</PropertyItem>
          {selectedCrystal.zodiac && (
            <PropertyItem>{selectedCrystal.zodiac.join('')}</PropertyItem>
          )}
          {selectedCrystal.chakra && (
            <PropertyItem>{selectedCrystal.chakra}</PropertyItem>
          )}
        </PropertyList>
      </Section>

      {descriptions.length > 0 && (
        <Section>
          <SectionTitle></SectionTitle>
          <PropertyList>
            {descriptions.map((desc, index) => (
              <PropertyItem key={index}>{desc}</PropertyItem>
            ))}
          </PropertyList>
        </Section>
      )}
    </InfoContainer>
  );
};

export default ProductInfo; 